import { createAsyncThunk, createEntityAdapter,createSlice } from "@reduxjs/toolkit";
import agent from "../../../app/api/agent";
import { Coupon } from "../../../app/models/Coupon";
import { MetaData } from "../../../app/models/Pagination";
import { RootState } from "../../../app/store/configureStore";


interface couponstate {
    couponLoaded: boolean;
    status: string;
    metaData: MetaData | null;
}

const couponAdapter = createEntityAdapter<Coupon>();
 
export const fetchCouponsAsync = createAsyncThunk<Coupon[]>(
    'coupon/fetchcouponsAsync',
    async (_, thunkAPI) => {
        try {
            return await agent.Coupon.list();
        } catch (error: any) {
            return thunkAPI.rejectWithValue({ error: error.data }) // Interceptor
        }
    }
)

export const fetchcouponAsync = createAsyncThunk<Coupon,number>(
    'coupon/fetchcouponAsync',
    async (couponId, thunkAPI) => {
        try {
            return await agent.Coupon.details(couponId);
        } catch (error: any) {
            return thunkAPI.rejectWithValue({ error: error.data }) // Interceptor
        }
    }
)

export const couponSlice = createSlice({
    name: 'coupon',
    initialState:couponAdapter.getInitialState({
        couponLoaded: false,
        status: 'idle',
        metaData: null,
    }),
    reducers: {
        setCoupon: (state, action) => {
            couponAdapter.upsertOne(state, action.payload);
            state.couponLoaded = false;
        },
        removeCoupon: (state, action) => {
            couponAdapter.removeOne(state, action.payload);
            state.couponLoaded = false; 
        },
        setMetaData: (state, action) => {
            state.metaData = action.payload;
        },
    },
    extraReducers: (builder => {
        builder.addCase(fetchCouponsAsync.pending, (state) => {
            state.status = 'pendingFetchcoupon';
        });
        builder.addCase(fetchCouponsAsync.fulfilled, (state, action) => {
            couponAdapter.setAll(state, action.payload);
            state.status = 'idle';
            state.couponLoaded = true;
        });
        builder.addCase(fetchCouponsAsync.rejected, (state, action) => {
            state.status = 'idle';
            console.log(action.payload)
        });

        builder.addCase(fetchcouponAsync.pending, (state) => {
            state.status = 'pendingFetchcoupon';
        });
        builder.addCase(fetchcouponAsync.fulfilled, (state, action) => {
            couponAdapter.upsertOne(state, action.payload);
            state.status = 'idle';
        });
        builder.addCase(fetchcouponAsync.rejected, (state, action) => {
            state.status = 'idle';
            console.log(action.payload)
        });
    })
})
export const couponSlectors = couponAdapter.getSelectors((state: RootState) => state.coupon); 

export const { setCoupon,removeCoupon,setMetaData} = couponSlice.actions
