import { useEffect } from "react";
import { categorySelectors, fetchCategorysAsync } from "../../features/admin/categoryCrud/categorySlice";
import { useAppSelector, useAppDispatch } from "../store/configureStore";
 
export default function useCategory() {
    const categorys = useAppSelector(categorySelectors.selectAll);
    const { categorysLoaded,metaData } = useAppSelector(state => state.category);
    const dispatch = useAppDispatch();
 
    useEffect(() => {
        if (!categorysLoaded) dispatch(fetchCategorysAsync());
    }, [categorysLoaded, dispatch])

    return {
        categorysLoaded,
        categorys,
        metaData,
    }
}
