import { ShoppingCart } from '@mui/icons-material';
import { Toolbar, Box, IconButton, Badge, Grid, MenuItem, CardMedia } from '@mui/material'
import { createBrowserHistory } from 'history';
import React from 'react'
import { Link, NavLink } from 'react-router-dom'
import { signOut } from '../../features/account/accountSlice';
import { clearBasket } from '../../features/basket/basketSlice';
import { useAppDispatch, useAppSelector } from '../store/configureStore';
import MenuAccount from './MenuAccount';
import MenuCrud from './MenuCrud';
import Logo from '../../Image/header/main.png'

export default function Header(props: any) {

  const { user } = useAppSelector(state => state.account);
  const { basket } = useAppSelector(state => state.basket);
  const itemCount = basket?.items.reduce((sum, item) => sum + item.quantity, 0);

  return (
    <>
      <Toolbar sx={{
        display: "flex",
        justifyContent: "space-between",
        alignItems: "center",
        bgcolor: "#00796b",
        color: "white"
      }}>
        <Box sx={{ flexGrow: 1 }} display="flex" alignItems="center">
        <Grid
          container
          direction="row"
          justifyContent="space-between"
          alignItems="flex-start"
        >
            <Grid> 
              <IconButton
                sx={{ color: "inherit", textDecoration: "none", fontFamily: "initial" }}
                component={NavLink} to="/"
              >
                <CardMedia
                  component="img"
                  image={Logo}
                  alt="Cactus"
                  height={80}
                />
              </IconButton>
              <IconButton
                sx={{ color: "inherit", textDecoration: "none", fontFamily: "initial" }}
                component={NavLink} to="/"
              >
                Home
              </IconButton>

              <IconButton
                sx={{ color: "inherit", textDecoration: "none", fontFamily: "initial" }}
                component={NavLink} to="/product"
              >
                Product
              </IconButton>
              <IconButton
                sx={{ color: "inherit", textDecoration: "none", fontFamily: "initial" }}
                component={NavLink} to="/coupon"
              >
                Coupon
              </IconButton>
            </Grid>

            <Grid sx={{mt:3}}>
              <IconButton
                component={Link}
                to="/basket"
                aria-label="cart"
                sx={{ color: "inherit" }}
                size="large"
              >
                <Badge badgeContent={itemCount} color="error">
                  <ShoppingCart />
                </Badge>
              </IconButton>

              {user && user.roles?.includes('Admin') && (
                <MenuCrud />
              )}

              {user ? (
                <MenuAccount />
              ) : (
                <>
                  <IconButton
                    sx={{ color: "inherit", textDecoration: "none", fontFamily: "initial" }}
                    component={NavLink} to="/register"
                  >
                    Register
                  </IconButton>
                  <IconButton
                    sx={{ color: "inherit", textDecoration: "none", fontFamily: "initial" }}
                    component={NavLink} to="/login"
                  >
                    Login
                  </IconButton>
                </>
              )}
            </Grid>
          </Grid>
        </Box>
      </Toolbar>
      <Toolbar />
    </>
  )
}
