import { yupResolver } from "@hookform/resolvers/yup";
import { LoadingButton } from "@mui/lab";
import { Box, Paper, Typography, Grid, Container, Button } from "@mui/material";
import { useEffect } from "react";
import { useForm, FieldValues } from "react-hook-form";
import agent from "../../../app/api/agent";
import { AppDropzone } from "../../../app/components/AppDropzone";
import AppSelectList from "../../../app/components/AppSelectList";
import TextInput from "../../../app/components/TextInput";
import useProducts from "../../../app/hook/useProducts";
import { useAppDispatch } from "../../../app/store/configureStore";
import { setProduct } from "../../product/productSlice";
import { validationSchema } from "./sourceValidation";
import { setSource } from "./sourceSlice";
import { Source } from "../../../app/models/Product";
import useSource from "../../../app/hook/useSource";


interface Props {
  source?: Source;   // ?   undefined
  cancelEdit: () => void;
}
 
export default function ProductForm({ source, cancelEdit }: Props) {
  const { 
    control, 
    reset, 
    handleSubmit, 
    formState: { isSubmitting },
  } = useForm({
    resolver: yupResolver(validationSchema),
  });
  
  const { } = useSource();

  const dispatch = useAppDispatch();

  useEffect(() => {
    if (source) reset(source);  // reset
  }, [source, reset]);


  async function handleSubmitData(data: FieldValues) {
    try {
      console.log(data)
      let response: Source;
      if (source) {
        response = await agent.Admin.updateSource(data);
      } else {
        response = await agent.Admin.createSource(data);
      }
      dispatch(setSource(response));
      cancelEdit();
    } catch (error) {
        console.log(error);
    }
  }
  return (
    <Container>
    <Box component={Paper} sx={{ p: 4 }}>
      <Typography variant="h4" gutterBottom sx={{ mb: 4 }}>
        {source ? (<>EditSource</>):(<>CreatSource</>)}

      </Typography>
      <form onSubmit={handleSubmit(handleSubmitData)}>
        <Grid container spacing={3}>
          <Grid item xs={12} sm={12}>
            <TextInput control={control} name="name" label="Product name" defaultValue=""/>
          </Grid>
        </Grid>
        <Box display="flex" justifyContent="space-between" sx={{ mt: 3 }}>
          <Button variant="contained" color="inherit" onClick={cancelEdit}>
            Cancel
          </Button>
          
          <LoadingButton
              loading={isSubmitting}
              variant="contained"
              color="success"
              type="submit"
            >
              Submit
          </LoadingButton>
        </Box>
      </form>
    </Box>
    </Container>
  );
}
