import { useEffect } from "react";
import { fetchReviewsAsync, reviewSelctors } from "../../features/admin/reviewCrud/reviewSlice";
import { useAppSelector, useAppDispatch } from "../store/configureStore";
 
export default function useReview() {
    const reviews = useAppSelector(reviewSelctors.selectAll);
    const { reviewLoaded,metaData } = useAppSelector(state => state.review);
    const dispatch = useAppDispatch();
 
    // useEffect  [] ( Redux dev tools)
    useEffect(() => {
        if (!reviewLoaded) dispatch(fetchReviewsAsync());
    }, [reviewLoaded, dispatch])

    return {
        reviews,
        reviewLoaded,
        metaData
    }
}
