import { Elements } from '@stripe/react-stripe-js'
import { loadStripe } from '@stripe/stripe-js';
import { useEffect, useState } from 'react';
import agent from '../../app/api/agent';
import Loading from '../../app/layout/Loading';
import { useAppDispatch } from '../../app/store/configureStore';
import { setBasket } from '../basket/basketSlice';
import PayPage from './PayPage';
 
//public key from stripe
const stripePromise = loadStripe('pk_test_51M9MlsJxcjU0WNCYz4AeYlXQOaqiG7jB1q8z8HAniqzjB4uJ3LXLLHP84GC8Qdszd9EjOmV5q7ZQIhGSFsSERJYD00tJo5YRF8')
 
export default function CheckoutWrapper() {
  const dispatch = useAppDispatch();
  const [loading, setLoading] = useState(true);
  
  // Stripe (incomplete)
  useEffect(() => {
    agent.Payments.createPaymentIntent()
        .then(basket => dispatch(setBasket(basket)))
        .catch(error => console.log(error))
        .finally(() => setLoading(false));
  }, [dispatch]);

  if (loading) return <Loading message='Loading checkout...' />
  return (
    <Elements stripe={stripePromise}>
      <PayPage />
    </Elements>
  )
}
