import axios, { AxiosError, AxiosResponse } from "axios"
import { createBrowserHistory } from "history";
import { toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';
import { PaginatedResponse } from "../models/Pagination";
import { store } from "../store/configureStore";
 
axios.defaults.baseURL = "http://localhost:5000/api/"
axios.defaults.withCredentials = true;

const history = createBrowserHistory();

const sleep = () => new Promise(resolve => setTimeout(resolve, 350)); //delay
 
const responseBody = (response: AxiosResponse) => response.data;

axios.interceptors.request.use((config: any) => {
    const token = store.getState().account.user?.token; // State 
    if (token) config.headers.Authorization = `Bearer ${token}`;
    return config;
})

axios.interceptors.response.use(async response => {
    
    await sleep()

    console.log(response)
    const pagination = response.headers['pagination']; // ProductController
    if (pagination) {
        response.data = new PaginatedResponse(response.data, JSON.parse(pagination));
        console.log(response)
        return response;
    }

    return response
}, (error: AxiosError) => {
    console.log('caught by interceptor')
    const { data, status } = error.response!
    var json = JSON.stringify(data)
    var result = JSON.parse(json) // object
 
    switch (status) {
        case 400:
            // GetValidationError()
            if (result.errors) {
                const modelStateErrors: string[] = [];
                for (const key in result.errors) {
                    if (result.errors[key]) {
                        modelStateErrors.push(result.errors[key])
                    }
                }
                throw modelStateErrors.flat();
            }
            toast.error(result.title);
            break;
        case 401:
            toast.error(result.title || 'Unauthorized');
            break;
        case 403:
            toast.error('You are not allowed to do that!');
            break;
        case 500:
            history.push('/server-error', { state: data })
            toast.error(result.title);
            // window.location.href = "/server-error"
            break;
        default:
            break;
    }
 
    return Promise.reject(error.response) // catch(error) 
})
 
const requests = {
    get: (url: string,params?: URLSearchParams) => axios.get(url,{params}).then(responseBody),
    post: (url: string, body: {}) => axios.post(url, body).then(responseBody),
    put: (url: string, body: {}) => axios.put(url, body).then(responseBody),
    delete: (url: string) => axios.delete(url).then(responseBody),
    postForm: (url: string, data: FormData) => axios.post(url, data, {
        headers: { 'Content-type': 'multipart/form-data' }
    }).then(responseBody),
    putForm: (url: string, data: FormData) => axios.put(url, data, {
        headers: { 'Content-type': 'multipart/form-data' }
    }).then(responseBody)
}
 
const TestErrors = {
    get400Error: () => requests.get('buggy/GetBadRequest'),
    get401Error: () => requests.get('buggy/GetUnAuthorized'),
    get404Error: () => requests.get('buggy/GetNotFound'),
    get500Error: () => requests.get('buggy/GetServerError'),
    getValidationError: () => requests.get('buggy/GetValidationError'),
}
 
const Product = {
    list: (params: URLSearchParams) => requests.get('products',params),
    details: (id: number) => requests.get(`products/${id}`),
    filters: () => requests.get('products/filters'),
}
const Category = {
    list: () => requests.get('category'),
    details: (id: number) => requests.get(`category/${id}`),
}
const Source = {
    list: () => requests.get('source'),
    details: (id: number) => requests.get(`source/${id}`),
}
const Basket = {
    get: () => requests.get('basket'),
    addItem: (productId: number, quantity = 1) => requests.post(`basket?productId=${productId}&quantity=${quantity}`, {}),
    removeItem: (productId: number, quantity = 1) => requests.delete(`basket?productId=${productId}&quantity=${quantity}`)
}

const AddImageProduct = {
    list: () => requests.get('addimageproduct'),
    details: (id: number) => requests.get(`addimageproduct/${id}`),
}

const Review = {
    list: () => requests.get('review'),
    details: (id: number) => requests.get(`review/${id}`),
}

const Coupon = {
    list: () => requests.get('coupon'),
    details: (id: number) => requests.get(`coupon/${id}`),
}

const Account = {
    login: (values: any) => requests.post('account/login', values),
    register: (values: any) => requests.post('account/register', values),
    currentUser: () => requests.get('account/currentUser'),
    fetchAddress: () => requests.get('account/savedAddress')
}

const Orders = {
    list: () => requests.get('order'),
    fetch: (id: number) => requests.get(`order/${id}`),
    create: (values: any) => requests.post('order', values)
}
const Payments = {
    createPaymentIntent: () => requests.post('payments', {})
}

function createFormData(item: any) {
    let formData = new FormData();
    for (const key in item) {
        formData.append(key, item[key])
    }
    return formData;
}
 
const Admin = {
    createProduct: (product: any) => requests.postForm('products', createFormData(product)),
    updateProduct: (product: any) => requests.putForm('products', createFormData(product)),
    deleteProduct: (id: number) => requests.delete(`products/${id}`),

    createCategory: (category: any) => requests.postForm('category', createFormData(category)),
    updateCategory: (category: any) => requests.putForm('category', createFormData(category)),
    deleteCategory: (id: number) => requests.delete(`category/${id}`),

    createSource: (source: any) => requests.postForm('source', createFormData(source)),
    updateSource: (source: any) => requests.putForm('source', createFormData(source)),
    deleteSource: (id: number) => requests.delete(`source/${id}`),

    createCoupon: (coupon: any) => requests.postForm('coupon', createFormData(coupon)),
    updateCoupon: (coupon: any) => requests.putForm('coupon', createFormData(coupon)),
    deleteCoupon: (id: number) => requests.delete(`coupon/${id}`),

    createReview: (review: any) => requests.postForm('review', createFormData(review)),
    updateReview: (review: any) => requests.putForm('review', createFormData(review)),
    deleteReview: (id: number) => requests.delete(`review/${id}`),
}

const agent = {
    TestErrors,
    Product,
    Category,
    Source,
    AddImageProduct,
    Review,
    Basket,
    Account,
    Orders,
    Coupon,
    Payments,
    Admin
}
 
export default agent;