import { createAsyncThunk, createEntityAdapter,createSlice } from "@reduxjs/toolkit";
import agent from "../../../app/api/agent";
import { MetaData } from "../../../app/models/Pagination";
import { Source } from "../../../app/models/Product";
import { RootState } from "../../../app/store/configureStore";


interface SourceState {
    sourcesLoaded: boolean;
    status: string;
    metaData: MetaData | null;
}

const SourcesAdapter = createEntityAdapter<Source>();
 
export const fetchSourcesAsync = createAsyncThunk<Source[]>(
    'Source/fetchSourcesAsync',
    async (_, thunkAPI) => {
        try {
            return await agent.Source.list();
        } catch (error: any) {
            return thunkAPI.rejectWithValue({ error: error.data }) // Interceptor
        }
    }
)

export const fetchSourceAsync = createAsyncThunk<Source,number>(
    'Source/fetchSourceAsync',
    async (SourceId, thunkAPI) => {
        try {
            return await agent.Source.details(SourceId);
        } catch (error: any) {
            return thunkAPI.rejectWithValue({ error: error.data }) // Interceptor
        }
    }
)

export const sourceSlice = createSlice({
    name: 'Source',
    initialState:SourcesAdapter.getInitialState({
        sourcesLoaded: false,
        status: 'idle',
        metaData: null,
    }),
    reducers: {
        setSource: (state, action) => {
            SourcesAdapter.upsertOne(state, action.payload);
            state.sourcesLoaded = false;
        },
        removeSource: (state, action) => {
            SourcesAdapter.removeOne(state, action.payload);
            state.sourcesLoaded = false; 
        },
        setMetaData: (state, action) => {
            state.metaData = action.payload;
        },
    },
    extraReducers: (builder => {
        builder.addCase(fetchSourcesAsync.pending, (state) => {
            state.status = 'pendingFetchSource';
        });
        builder.addCase(fetchSourcesAsync.fulfilled, (state, action) => {
            SourcesAdapter.setAll(state, action.payload);
            state.status = 'idle';
            state.sourcesLoaded = true;
        });
        builder.addCase(fetchSourcesAsync.rejected, (state, action) => {
            state.status = 'idle';
            console.log(action.payload)
        });

        builder.addCase(fetchSourceAsync.pending, (state) => {
            state.status = 'pendingFetchSource';
        });
        builder.addCase(fetchSourceAsync.fulfilled, (state, action) => {
            SourcesAdapter.upsertOne(state, action.payload);
            state.status = 'idle';
        });
        builder.addCase(fetchSourceAsync.rejected, (state, action) => {
            state.status = 'idle';
            console.log(action.payload)
        });
    })
})
export const SourceSelectors = SourcesAdapter.getSelectors((state: RootState) => state.source); 

export const { setSource,removeSource,setMetaData} = sourceSlice.actions
