import * as React from "react";
import Button from "@mui/material/Button";
import Menu from "@mui/material/Menu";
import MenuItem from "@mui/material/MenuItem";
import { Link } from "react-router-dom";
import AdminPanelSettingsIcon from '@mui/icons-material/AdminPanelSettings';
export default function SignedinMenu() {
  const [anchorEl, setAnchorEl] = React.useState<null | HTMLElement>(null);
  const open = Boolean(anchorEl);
 
  const handleClick = (event: React.MouseEvent<HTMLButtonElement>) => {
    setAnchorEl(event.currentTarget);
  };
  const handleClose = () => {
    setAnchorEl(null);
  };
 
  return (
    <>
      <Button onClick={handleClick} color="inherit" sx={{ typography: "h6" }}>
        <AdminPanelSettingsIcon/>ADMIN
      </Button>
      <Menu
        anchorEl={anchorEl}
        open={open}
        onClose={handleClose}
        MenuListProps={{
          "aria-labelledby": "basic-button",
        }}
      >
        <MenuItem onClick={handleClose} component={Link} to="/productcrud">
            Product
        </MenuItem>
        <MenuItem onClick={handleClose} component={Link} to="/categorycrud">
            Category
        </MenuItem>
        <MenuItem onClick={handleClose} component={Link} to="/sourcecrud">
            Source
        </MenuItem>
        <MenuItem onClick={handleClose} component={Link} to="/categorycrud">
            -
        </MenuItem>
      </Menu>
    </>
  );
}
