import { Edit, Delete } from '@mui/icons-material';
import { LoadingButton } from '@mui/lab';
import { Box, Button, Card, CardContent, CardHeader, Container, Grid, Typography } from '@mui/material';
import React, { useEffect, useState } from 'react'
import agent from '../../app/api/agent';
import useCoupon from '../../app/hook/useCoupon';
import { Coupon } from '../../app/models/Coupon';
import { useAppDispatch, useAppSelector } from '../../app/store/configureStore';
import CouponCU from '../admin/couponCrud/CouponCU';
import { removeCoupon } from '../admin/couponCrud/couponSlice';

export default function CouponPage() {
    const { coupons,metaData } = useCoupon();
  const dispatch = useAppDispatch();
  const { user } = useAppSelector(state => state.account);

  const [editMode,setEditMode] = useState(false)
  const [selectedCoupon, setSelectedCoupon] = useState<Coupon | undefined>(undefined);
  const [loading, setLoading] = useState(false);
  const [target, setTarget] = useState(0);

  // Edit
  function handleSelectCoupon(coupon: Coupon) {
      setSelectedCoupon(coupon);
      setEditMode(true);
  }
  
  function handleDeleteCoupon(id: number) {
    setLoading(true);
    setTarget(id);
    agent.Admin.deleteCoupon(id)
      .then(() => dispatch(removeCoupon(id)))
      .catch((error) => console.log(error))
      .finally(() => setLoading(false));
  }

  function cancelEdit() {
      if (selectedCoupon) setSelectedCoupon(undefined);
      setEditMode(false);
  }

  if (editMode) return <CouponCU coupon={selectedCoupon} cancelEdit={cancelEdit} />

    return (
        <Container>
            <Card sx={{ padding: 2 }}>
                <Typography variant="h2" sx={{ fontFamily: "initial" }}>CouponList</Typography>
                <Typography>The code is used to reduce the total price of the product.</Typography>
                <Typography>&nbsp;</Typography>
                {user && user.roles?.includes('Admin') && (
                    <Button sx={{ ml:125,mt:-25}} size="large" variant="contained" onClick={() => setEditMode(true)} color="success">
                        Create
                    </Button>
                )}
                
                <Grid container spacing={4}>
                    {coupons.map((coupon,index) => (
                        <Grid item xs={3} key={index}>
                            <Card sx={{ maxWidth: 500, mt: 2, textAlign: "center" }}>
                                {coupon.quantityCode < 10 && coupon.quantityCode > 0 ?
                                    (<>
                                        <Box>
                                            <Typography sx={{ border: 1 }} variant="subtitle1" color="error">
                                                HOT!!!
                                            </Typography>
                                        </Box>
                                    </>) :
                                    coupon.quantityCode === 0 ?
                                    (<>
                                        <Box>
                                            <Typography sx={{ border: 1 }} variant="subtitle2" color="error">
                                                COUPON SO OUT
                                            </Typography>
                                        </Box>
                                    </>) :
                                    (<>
                                        <Box>
                                            <Typography sx={{ border: 1 }} variant="subtitle1" color="green">
                                                Available Coupon
                                            </Typography>
                                        </Box>
                                    </>)
                                }

                                <CardHeader
                                    title={"Code : " + coupon.code}
                                />
                                <CardContent>
                                    <Typography variant="body2" color="text.secondary">
                                        percentage : {coupon.percentage}%
                                    </Typography>
                                    {coupon.quantityCode === 0 ? (
                                        <>
                                            <Typography variant="body2" color="error">
                                                quantityCode : {coupon.quantityCode}
                                            </Typography>
                                        </>
                                    ) : (
                                        <>
                                            <Typography variant="body2" color="text.secondary">
                                                quantityCode : {coupon.quantityCode}
                                            </Typography>
                                        </>
                                    )}
                                    <Typography variant="body2" color="text.secondary">
                                        date : {coupon.date.split("T")[0]}
                                    </Typography>

                                </CardContent>
                                {user && user.roles?.includes('Admin') && (
                                    <>
                                    <Button startIcon={<Edit />} onClick={()=>handleSelectCoupon(coupon)} color="warning"/>
                                    <LoadingButton
                                        loading={loading && target === coupon.id}
                                        startIcon={<Delete />}
                                        color="error"
                                        onClick={() => handleDeleteCoupon(coupon.id)}
                                    />
                                    </>
                                )}
                            </Card>

                        </Grid>
                    ))}
                </Grid>
            </Card>
        </Container>
    )
}
