import * as React from 'react';
import Box from '@mui/material/Box';
import Paper from '@mui/material/Paper';
import Stepper from '@mui/material/Stepper';
import Step from '@mui/material/Step';
import StepLabel from '@mui/material/StepLabel';
import Button from '@mui/material/Button';
import Typography from '@mui/material/Typography';
import { FormProvider, useForm, FieldValues } from 'react-hook-form';
import AddressPay from './AddressPay';
import PayAllProduct from './PayAllProduct';
import PaymentPage from './PaymentPage';
import { validationSchema } from './augmentation/CheckValidation';
import { yupResolver } from "@hookform/resolvers/yup";
import { useAppDispatch, useAppSelector } from '../../app/store/configureStore';
import { clearBasket } from '../basket/basketSlice';
import agent from '../../app/api/agent';
import { LoadingButton } from '@mui/lab';
import { useEffect,useState } from 'react';
import { Container } from '@mui/material';
import { StripeElementType } from '@stripe/stripe-js';
import { useStripe, useElements, CardNumberElement } from '@stripe/react-stripe-js';

const steps = ['Address', 'AllProduct', 'Payment details'];


export default function CheckoutPage() {
  const [activeStep, setActiveStep] = useState(0);

  const [orderNumber, setOrderNumber] = useState(0);
  const [loading, setLoading] = useState(false);
  const dispatch = useAppDispatch();

  const [cardState, setCardState] = useState<{
    elementError: { [key in StripeElementType]?: string };
  }>({ elementError: {} });
  
  const [cardComplete, setCardComplete] = useState<any>({
    cardNumber: false,
    cardExpiry: false,
    cardCvc: false,
  });
  
  //#region  Order  Stripe
  const [paymentMessage, setPaymentMessage] = useState("");
  const [paymentSucceeded, setPaymentSucceeded] = useState(false);
  const { basket } = useAppSelector((state) => state.basket);
  const stripe = useStripe();
  const elements = useElements(); //
  //#endregion
  
  function onCardInputChange(event: any) {
    setCardState({
      ...cardState,
      elementError: {
        ...cardState.elementError,
        [event.elementType]: event.error?.message,
      },
    });
    setCardComplete({ ...cardComplete, [event.elementType]: event.complete });
  }
  
  function submitDisabled(): boolean {
    if (activeStep === steps.length - 1) {
      return (
        !cardComplete.cardCvc ||
        !cardComplete.cardExpiry ||
        !cardComplete.cardNumber ||
        !methods.formState.isValid
      );
    } else {
      return !methods.formState.isValid;
    }
  }
  function getStepContent(step: number) {
    
  
    switch (step) {
      case 0:
        return <AddressPay />;
      case 1:
        return <PayAllProduct />;
      case 2:
        return (
        <PaymentPage
          cardState={cardState}
          onCardInputChange={onCardInputChange}
        />
      );
  
      default:
        throw new Error('Unknown step');
    }
  }

    //#region  Order  Order  Stripe.com
  async function submitOrder(data: FieldValues) {
    setLoading(true);
    const { nameOnCard, saveAddress,useCoupon,usePoint, ...shippingAddress } = data;
    if (!stripe || !elements) return; // stripe not ready
    try {
      const cardElement = elements.getElement(CardNumberElement);
      const paymentResult = await stripe.confirmCardPayment(
        basket?.clientSecret!,
        {
          payment_method: {
            card: cardElement!,
            billing_details: {
              name: nameOnCard,
            },
          },
        }
      );
      console.log(paymentResult);
      if (paymentResult.paymentIntent?.status === "succeeded") {
        const orderNumber = await agent.Orders.create({
          saveAddress,
          shippingAddress,
          useCoupon,
          usePoint
        });
        setOrderNumber(orderNumber);
        setPaymentSucceeded(true);
        setPaymentMessage("Thank you - we have received your payment");
        setActiveStep(activeStep + 1);
        dispatch(clearBasket());
        setLoading(false);
      } else {
        setPaymentMessage(paymentResult.error?.message!);
        setPaymentSucceeded(false);
        setLoading(false);
        setActiveStep(activeStep + 1);
      }
    } catch (error) {
      console.log(error);
      setLoading(false);
    }
  }
 
  const handleNext = async (data: FieldValues) => {
    if (activeStep === steps.length - 1) {
      await submitOrder(data);
    } else {
      setActiveStep(activeStep + 1);
    }
  };
  //#region


  const handleBack = () => {
    setActiveStep(activeStep - 1);
  };

  const currentValidationSchema = validationSchema[activeStep];

  const methods = useForm({
    mode: "all",
    resolver: yupResolver(currentValidationSchema),
  });

  // User
  useEffect(() => {
    agent.Account.fetchAddress()
      .then(response => {
        if (response) {
          //reset  Form , saveAddress: false  
          methods.reset({ ...methods.getValues(), ...response, saveAddress: false })
        }
      })
  }, [methods])

  return (
    <Container>
      <FormProvider {...methods}>
        <Paper variant="outlined" sx={{ my: { xs: 3, md: 6 }, p: { xs: 2, md: 3 } ,borderRadius:10}}>
          <Typography component="h1" variant="h4" align="center">
            PAYMENT
          </Typography>
          <Stepper activeStep={activeStep} sx={{ pt: 3, pb: 5 }}>
            {steps.map((label) => (
              <Step key={label}>
                <StepLabel>{label}</StepLabel>
              </Step>
            ))}
          </Stepper>
          <React.Fragment>
            {activeStep === steps.length ? (
               <>
                <Typography variant="h5" gutterBottom>
                  {paymentMessage}
                </Typography>
                {paymentSucceeded ? (
                  <Typography variant="subtitle1">
                    Your order number is #{orderNumber}. We have not emailed your
                    order confirmation, and will not send you an update when your
                    order has shipped as this is a fake store!.
                  </Typography>
                ) : (
                  <Button variant="contained" onClick={handleBack}>
                    Go back and try again
                  </Button>
                )}
              </>
            ) : (
              <form onSubmit={methods.handleSubmit(handleNext)}>
                {getStepContent(activeStep)}
                <Box sx={{ display: 'flex', justifyContent: 'flex-end' }}>
                  {activeStep !== 0 && (
                    <Button color="success" onClick={handleBack} sx={{ mt: 3, ml: 1 }}>
                      Back
                    </Button>
                  )}
                  <LoadingButton
                    loading={loading}
                    disabled={submitDisabled()}
                    variant="contained"
                    type="submit"
                    color="success"
                    sx={{ mt: 3, ml: 1 }}
                  >
                    {activeStep === steps.length - 1 ? "Place order" : "Next"}
                  </LoadingButton>
                </Box>
              </form>
            )}
          </React.Fragment>
        </Paper>
      </FormProvider>
    </Container>
  );
}
