import { useEffect } from "react";
import { categorySelectors } from "../../features/admin/categoryCrud/categorySlice";
import { fetchSourcesAsync, SourceSelectors } from "../../features/admin/sourceCrud/sourceSlice";
import { useAppSelector, useAppDispatch } from "../store/configureStore";
 
export default function useCategory() {
    const sources = useAppSelector(SourceSelectors.selectAll);
    const { sourcesLoaded,metaData } = useAppSelector(state => state.source);
    const dispatch = useAppDispatch();
 
    useEffect(() => {
        if (!sourcesLoaded) dispatch(fetchSourcesAsync());
    }, [sourcesLoaded, dispatch])

    return {
        sourcesLoaded,
        sources,
        metaData,
    }
}
