import { Source } from "@mui/icons-material";
import { configureStore } from "@reduxjs/toolkit";
import { useDispatch, TypedUseSelectorHook, useSelector } from "react-redux";
import { accountSlice } from "../../features/account/accountSlice";
import { categorySlice } from "../../features/admin/categoryCrud/categorySlice";
import { couponSlice } from "../../features/admin/couponCrud/couponSlice";
import { reviewSlice } from "../../features/admin/reviewCrud/reviewSlice";
import { sourceSlice } from "../../features/admin/sourceCrud/sourceSlice";
import { basketSlice } from "../../features/basket/basketSlice";
import { productSlice } from "../../features/product/productSlice";

export const store = configureStore({
  reducer: {
    basket: basketSlice.reducer,
    product: productSlice.reducer,
    account: accountSlice.reducer,
    category: categorySlice.reducer,
    source: sourceSlice.reducer,
    coupon: couponSlice.reducer,
    review: reviewSlice.reducer
  },
});

export type RootState = ReturnType<typeof store.getState>; 
export type AppDispatch = typeof store.dispatch; 

export const useAppDispatch = () => useDispatch<AppDispatch>();
export const useAppSelector: TypedUseSelectorHook<RootState> = useSelector;
