import { yupResolver } from "@hookform/resolvers/yup";
import { LoadingButton } from "@mui/lab";
import { Box, Paper, Typography, Grid, Container, Button } from "@mui/material";
import { useEffect } from "react";
import { useForm, FieldValues } from "react-hook-form";
import agent from "../../../app/api/agent";
import { AppDropzone } from "../../../app/components/AppDropzone";
import AppSelectList from "../../../app/components/AppSelectList";
import TextInput from "../../../app/components/TextInput";
import useCategory from "../../../app/hook/useCategory";
import useProducts from "../../../app/hook/useProducts";
import useSource from "../../../app/hook/useSource";
import { Product } from "../../../app/models/Product";
import { useAppDispatch, useAppSelector } from "../../../app/store/configureStore";
import { setProduct } from "../../product/productSlice";
import SelectCategory from "../appSelectList/SelectCategory";
import SelectSource from "../appSelectList/SelectSource";
import { validationSchema } from "./productValidation";

interface Props {
  product?: Product;   // ?   undefined
  cancelEdit: () => void;
}
 
export default function ProductForm({ product, cancelEdit }: Props) {
  const { 
    control, 
    reset, 
    handleSubmit, 
    watch,
    formState: { isDirty, isSubmitting },
  } = useForm({
    resolver: yupResolver(validationSchema),
  });

  const watchFile = watch("file", null);
  const dispatch = useAppDispatch();

  useEffect(() => {
    if (product && !watchFile && !isDirty) reset(product);  
    return () => {
      if (watchFile) URL.revokeObjectURL(watchFile.preview); // URL 
    };
  }, [product, reset, watchFile, isDirty]);

  async function handleSubmitData(data: FieldValues) {
    try {
      console.log(data)
      let response: Product;
      if (product) {
        response = await agent.Admin.updateProduct(data);
      } else {
        response = await agent.Admin.createProduct(data);
      }
      dispatch(setProduct(response));
      cancelEdit();
    } catch (error) {
        console.log(error);
    }
  }

  const { categorys } = useCategory();
  const { sources } = useSource();
  
  return (
    <Container>
    <Box component={Paper} sx={{ p: 4 }}>
      <Typography variant="h4" gutterBottom sx={{ mb: 4 }}>
        {product ? (<>EditProduct</>):(<>CreatProduct</>)}
      </Typography>
      <form onSubmit={handleSubmit(handleSubmitData)}>
        <Grid container spacing={3}>
          <Grid item xs={12} sm={4}>
            <TextInput control={control} name="name" label="Product name" defaultValue=""/>
          </Grid>
         
          <Grid item xs={12}>
            <TextInput
              control={control}
              multiline={true}
              rows={4}
              name="detail"
              label="Detail" 
              defaultValue=""
            />
          </Grid>
          <Grid item xs={12} sm={6}>
          <SelectCategory
              control={control}
              items={categorys}
              name="categoryId"
              label="Category"
            />
          </Grid>
          <Grid item xs={12} sm={6}>
          <SelectSource
              control={control}
              items={sources}
              name="sourceId"
              label="Source"
            />
          </Grid>
          <Grid item xs={12} sm={6}>
            <TextInput control={control} type="number" name="price" label="Price" defaultValue=""/>
          </Grid>
          <Grid item xs={12} sm={6}>
            <TextInput
              control={control}
              type="number"
              name="quantityInStock"
              label="Quantity in Stock" 
              defaultValue=""
            />
          </Grid>
          
          <Grid item xs={12}>
          <Grid item xs={12}>
              <Box
                display="flex"
                justifyContent="space-between"
                alignItems="center"
              >
                <AppDropzone control={control} name="file" />
                {watchFile ? (
                  <img
                    src={watchFile.preview}
                    alt="preview"
                    style={{ maxHeight: 200 }}
                  />
                ) : (
                  <img
                    src={product?.imageUrl}
                    alt={product?.name}
                    style={{ maxHeight: 200 }}
                  />
                )}
              </Box>
            </Grid>
          </Grid>
        </Grid>
        <Box display="flex" justifyContent="space-between" sx={{ mt: 3 }}>
          <Button variant="contained" color="inherit" onClick={cancelEdit}>
            Cancel
          </Button>
          
          <LoadingButton
              loading={isSubmitting}
              variant="contained"
              color="success"
              type="submit"
            >
              Submit
          </LoadingButton>
        </Box>
      </form>
    </Box>
    </Container>
  );
}
