import {
  Typography,  Button,  TableContainer,  Paper,  Table,  TableHead,  TableRow,  TableCell,
  TableBody,  Box, Container, Card,
} from "@mui/material";
import { Edit, Delete } from "@mui/icons-material";
import { useState } from "react";
import { LoadingButton } from "@mui/lab";
import { useAppDispatch } from "../../../app/store/configureStore";
import { Category, Product, Source } from "../../../app/models/Product";
import agent from "../../../app/api/agent";
import AppPagination from "../../product/Pagination";
import useCategory from "../../../app/hook/useCategory";

import { setPageNumber } from "../../product/productSlice";
import useSource from "../../../app/hook/useSource";
import SourceCU from "./SourceCU";
import { removeSource } from "./sourceSlice";
 
export default function SourceCrud() {
  const { sources,metaData } = useSource();
  const dispatch = useAppDispatch();

  const [editMode,setEditMode] = useState(false)
  const [selectedSource, setSelectedSource] = useState<Source | undefined>(undefined);
  const [loading, setLoading] = useState(false);
  const [target, setTarget] = useState(0);

  // Edit
  function handleSelectSource(source: Source) {
      setSelectedSource(source);
      setEditMode(true);
  }
  
  function handleDeleteSource(id: number) {
    setLoading(true);
    setTarget(id);
    agent.Admin.deleteSource(id)
      .then(() => dispatch(removeSource(id)))
      .catch((error) => console.log(error))
      .finally(() => setLoading(false));
  }

  function cancelEdit() {
      if (selectedSource) setSelectedSource(undefined);
      setEditMode(false);
  }

  if (editMode) return <SourceCU source={selectedSource} cancelEdit={cancelEdit} />

  return (
    <Container>
      <Card sx={{borderRadius:3}}>

      <Box display="flex" justifyContent="space-between">
        <Typography sx={{ p: 2 }} variant="h4">
          SourceList
        </Typography>
        <Button sx={{ m: 2 }} size="large" variant="contained" onClick={()=>setEditMode(true)} color="success">
          Create
        </Button>
      </Box>
      <TableContainer component={Paper}>
        <Table sx={{ minWidth: 650 }} aria-label="simple table">
          <TableHead>
            <TableRow>
              <TableCell>#</TableCell>
              <TableCell align="center">Source</TableCell>
              <TableCell align="center">Date</TableCell>
              <TableCell align="right"></TableCell>
            </TableRow>
          </TableHead>
          <TableBody>
            {sources.map((source,index) => (
              <TableRow
                key={index}
                sx={{ "&:last-child td, &:last-child th": { border: 0 } }}
              >
                <TableCell component="th" scope="row">
                  {source.id}
                </TableCell>
                <TableCell align="center">{source.name}</TableCell>
                <TableCell align="center">{source.date.split("T")[0]}</TableCell>
                <TableCell align="right">
                <Button startIcon={<Edit />} onClick={()=>handleSelectSource(source)} color="warning"/>
                  <LoadingButton
                    loading={loading && target === source.id}
                    startIcon={<Delete />}
                    color="error"
                    onClick={() => handleDeleteSource(source.id)}
                  />
                </TableCell>
              </TableRow>
            ))}
          </TableBody>
        </Table>
      </TableContainer>
      {metaData && (
        <Box sx={{ pt: 2 }}>
          <AppPagination
            metaData={metaData}
            onPageChange={(page: number) =>
              dispatch(setPageNumber({ pageNumber: page }))
            }
          />
        </Box>
      )}
      </Card>
    </Container>
  );
}
