import * as React from 'react';
import Grid from '@mui/material/Grid';
import Typography from '@mui/material/Typography';
import {useFormContext } from 'react-hook-form';
import CheckboxPlay from './augmentation/CheckboxPay';
import TextInput from '../../app/components/TextInput';
 
export default function AddressPay() {
  const {control,formState} = useFormContext()
 
  return (
    <React.Fragment>
      <Typography sx={{textAlign:"center"}} variant="h5" gutterBottom>
        Address
      </Typography>
      <Grid container spacing={3}>
        <Grid item xs={12} sm={6}>
          <TextInput control={control} name="fullName" label="Full name" defaultValue=""/>
        </Grid>
        <Grid item xs={12} sm={6}>
          <TextInput control={control} name="city" label="City" defaultValue=""/>
        </Grid>
        <Grid item xs={12} sm={12}>
          <TextInput
            control={control}
            name="address1"
            label="Address line 1"
            multiline
            rows={4}
            defaultValue=""
          />
        </Grid>
        <Grid item xs={12} sm={12}>
          <TextInput
            control={control}
            name="address2"
            label="Address line 2"
            multiline
            rows={4}
            defaultValue=""
          />
        </Grid>
        <Grid item xs={12} sm={4}>
          <TextInput control={control} name="state" label="State" defaultValue=""/>
        </Grid>
        <Grid item xs={12} sm={4}>
          <TextInput control={control} name="zip" label="Zipcode" defaultValue=""/>
        </Grid>
        <Grid item xs={12} sm={4}>
          <TextInput control={control} name="country" label="Country" defaultValue=""/>
        </Grid>

        <Grid item xs={12}>
          <CheckboxPlay
            disabled={!formState.isDirty}//
            control={control}
            name="saveAddress"
            label="Save this address after payment."
          />
        </Grid>
 
      </Grid>
    </React.Fragment>
  );
}
