import React, { useCallback, useEffect, useState } from 'react';
import { Container, createTheme, CssBaseline, ThemeProvider } from '@mui/material';
import Header from './Header';
import { Route, Routes } from 'react-router-dom';
import { ToastContainer } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";
import Products from '../../features/product/Products';
import ProductDetails from '../../features/product/ProductDetails';
import NotFound from '../errors/NotFound';
import ServerError from '../errors/ServerError';
import agent from '../api/agent';
import { getCookie } from '../util/util';
import { useStoreContext } from '../context/StoreContext';
import Loading from './Loading';
import BasketPage from '../../features/basket/BasketPage';
import HomePage from '../../features/home/HomePage';
import PayPage from '../../features/pay/PayPage';
import { useAppDispatch } from '../store/configureStore';
import { fetchBasketAsync, setBasket } from '../../features/basket/basketSlice';
import Register from '../../features/account/Register';
import Login from '../../features/account/Login';
import { fetchCurrentUser } from '../../features/account/accountSlice';
import { PrivateLogin, PrivateRoute } from './PrivateRoute';
import OrderPage from '../../features/pay/OrderPage';
import CouponPage from '../../features/coupon/CouponPage';
import PayWrapper from '../../features/pay/PayWrapper';
import ProductCrud from '../../features/admin/productCrud/ProductCrud';
import CategoryCrud from '../../features/admin/categoryCrud/CategoryCrud';
import SourceCrud from '../../features/admin/sourceCrud/SourceCrud';

function App() {
  const dispatch = useAppDispatch();
  const [loading, setLoading] = useState(true);
 
  const initApp = useCallback(async () => {
    try {
      await dispatch(fetchCurrentUser());
      await dispatch(fetchBasketAsync());
    } catch (error) {
      console.log(error);
    }
  }, [dispatch]);
 
  useEffect(() => {
    initApp().then(() => setLoading(false));
  }, [initApp]);

  const [mode, setMode] = useState(false);
    const modeDisplay = mode ? "dark" : "light";
  
    const handleMode = () => setMode(!mode);
  
    const theme = createTheme({
      palette: {
        mode: modeDisplay,
        background: {
          default: modeDisplay === "light" ? "#f0f5f7" : "#384348",
        },
      },
    });

    if (loading) return <Loading message="Initilize App....." />;
  return (
    <>
        <ThemeProvider theme={theme}>
        <ToastContainer
          position="bottom-right"
          hideProgressBar
          theme="colored"
        />
          <CssBaseline />
          <Header handleMode={handleMode} />
            <Routes>
              <Route path="/" element={<HomePage />} />
              <Route path="/product" element={<Products />} />
              <Route path="/product/:id" element={<ProductDetails />} />
              <Route path="/basket" element={<BasketPage />} />
              <Route path="/coupon" element={<CouponPage />} />

              <Route element={<PrivateRoute />}>
                <Route path="/pay" element={<PayWrapper />} />
                <Route path="/orders" element={<OrderPage/>}/>
                <Route path="/productcrud" element={<ProductCrud />} />
                <Route path="/categorycrud" element={<CategoryCrud />} />
                <Route path="/sourcecrud" element={<SourceCrud />} />
              </Route>
              
              <Route
                path="/login"
                element={
                  <PrivateLogin>
                    <Login />
                  </PrivateLogin>
                  }
                />

              <Route path="/register" element={<Register />} />
              <Route path="/server-error" element={<ServerError />} />
              <Route path="*" element={<NotFound />} />
            </Routes>
        </ThemeProvider>
    </>
  );
}

export default App;
