import { createAsyncThunk, createEntityAdapter,createSlice } from "@reduxjs/toolkit";
import agent from "../../../app/api/agent";
import { MetaData } from "../../../app/models/Pagination";
import { Category, ProductParams } from "../../../app/models/Product";
import { RootState } from "../../../app/store/configureStore";


interface CategoryState {
    categorysLoaded: boolean;
    status: string;
    metaData: MetaData | null;
}

const categorysAdapter = createEntityAdapter<Category>();
 
export const fetchCategorysAsync = createAsyncThunk<Category[]>(
    'category/fetchCategorysAsync',
    async (_, thunkAPI) => {
        try {
            return await agent.Category.list();
        } catch (error: any) {
            return thunkAPI.rejectWithValue({ error: error.data }) // Interceptor
        }
    }
)

export const fetchCategoryAsync = createAsyncThunk<Category,number>(
    'category/fetchCategoryAsync',
    async (categoryId, thunkAPI) => {
        try {
            return await agent.Category.details(categoryId);
        } catch (error: any) {
            return thunkAPI.rejectWithValue({ error: error.data }) // Interceptor
        }
    }
)

export const categorySlice = createSlice({
    name: 'category',
    initialState:categorysAdapter.getInitialState({
        categorysLoaded: false,
        status: 'idle',
        metaData: null,
    }),
    reducers: {
        setCategory: (state, action) => {
            categorysAdapter.upsertOne(state, action.payload);
            state.categorysLoaded = false;
        },
        removeCategory: (state, action) => {
            categorysAdapter.removeOne(state, action.payload);
            state.categorysLoaded = false; 
        },
        setMetaData: (state, action) => {
            state.metaData = action.payload;
        },
    },
    extraReducers: (builder => {
        builder.addCase(fetchCategorysAsync.pending, (state) => {
            state.status = 'pendingFetchCategory';
        });
        builder.addCase(fetchCategorysAsync.fulfilled, (state, action) => {
            categorysAdapter.setAll(state, action.payload);
            state.status = 'idle';
            state.categorysLoaded = true;
        });
        builder.addCase(fetchCategorysAsync.rejected, (state, action) => {
            state.status = 'idle';
            console.log(action.payload)
        });

        builder.addCase(fetchCategoryAsync.pending, (state) => {
            state.status = 'pendingFetchCategory';
        });
        builder.addCase(fetchCategoryAsync.fulfilled, (state, action) => {
            categorysAdapter.upsertOne(state, action.payload);
            state.status = 'idle';
        });
        builder.addCase(fetchCategoryAsync.rejected, (state, action) => {
            state.status = 'idle';
            console.log(action.payload)
        });
    })
})
export const categorySelectors = categorysAdapter.getSelectors((state: RootState) => state.category); 

export const { setCategory,removeCategory,setMetaData} = categorySlice.actions
