import {
  Typography, Button, TableContainer, Paper, Table, TableHead, TableRow, TableCell,
  TableBody, Box, Container, Card,
} from "@mui/material";
import { Edit, Delete } from "@mui/icons-material";
import { useState } from "react";
import ProductCU from "./ProductCU";
import { LoadingButton } from "@mui/lab";
import { removeProduct, setPageNumber } from "../../product/productSlice";
import { useAppDispatch } from "../../../app/store/configureStore";
import useProducts from "../../../app/hook/useProducts";
import { Product } from "../../../app/models/Product";
import agent from "../../../app/api/agent";
import { currencyFormat } from "../../../app/util/util";
import AppPagination from "../../product/Pagination";

export default function ProductCrud() {
  const { products, metaData } = useProducts();
  const dispatch = useAppDispatch();
  const [editMode, setEditMode] = useState(false)
  const [selectedProduct, setSelectedProduct] = useState<Product | undefined>(undefined);
  const [loading, setLoading] = useState(false);
  const [target, setTarget] = useState(0);

  // Edit
  function handleSelectProduct(product: Product) {
    setSelectedProduct(product);
    setEditMode(true);
  }

  function handleDeleteProduct(id: number) {
    setLoading(true);
    setTarget(id);
    agent.Admin.deleteProduct(id)
      .then(() => dispatch(removeProduct(id)))
      .catch((error) => console.log(error))
      .finally(() => setLoading(false));
  }

  function cancelEdit() {
    if (selectedProduct) setSelectedProduct(undefined);
    setEditMode(false);
  }

  if (editMode) return <ProductCU product={selectedProduct} cancelEdit={cancelEdit} />

  return (
    <Container>
      <Card sx={{borderRadius:3}}>
        <Box display="flex" justifyContent="space-between">
          <Typography sx={{ p: 2 }} variant="h4">
            ProductList
          </Typography>
          <Button sx={{ m: 2 }} size="large" variant="contained" onClick={() => setEditMode(true)} color="success">
            Create
          </Button>
        </Box>
        <TableContainer component={Paper}>
          <Table sx={{ minWidth: 650 }} aria-label="simple table">
            <TableHead>
              <TableRow>
                <TableCell>#</TableCell>
                <TableCell align="left">Product</TableCell>
                <TableCell align="right">Price</TableCell>
                <TableCell align="center">Category</TableCell>
                <TableCell align="center">Source</TableCell>
                <TableCell align="center">Quantity</TableCell>
                <TableCell align="right"></TableCell>
              </TableRow>
            </TableHead>
            <TableBody>
              {products.map((product, index) => (
                <TableRow
                  key={index}
                  sx={{ "&:last-child td, &:last-child th": { border: 0 } }}
                >
                  <TableCell component="th" scope="row">
                    {product.id}
                  </TableCell>
                  <TableCell align="left">
                    <Box display="flex" alignItems="center">
                      <img
                        src={product.imageUrl}
                        alt={product.name}
                        style={{ height: 80,width:80, marginRight: 20 }}
                      />
                      <span>{product.name}</span>
                    </Box>
                  </TableCell>
                  <TableCell align="right">
                    {currencyFormat(product.price)}
                  </TableCell>
                  <TableCell align="center">{product.category?.name}</TableCell>
                  <TableCell align="center">{product.source?.name}</TableCell>
                  <TableCell align="center">{product.quantityInStock}</TableCell>
                  <TableCell align="right">
                    <Button startIcon={<Edit />} onClick={() => handleSelectProduct(product)} color="warning"/>
                    <LoadingButton
                      loading={loading && target === product.id}
                      startIcon={<Delete />}
                      color="error"
                      onClick={() => handleDeleteProduct(product.id)}
                    />
                  </TableCell>
                </TableRow>
              ))}
            </TableBody>
          </Table>
        </TableContainer>
        {metaData && (
          <Box sx={{ pt: 2 }}>
            <AppPagination
              metaData={metaData}
              onPageChange={(page: number) =>
                dispatch(setPageNumber({ pageNumber: page }))
              }
            />
          </Box>
        )}
      </Card>
    </Container>
  );
}
