import { yupResolver } from "@hookform/resolvers/yup";
import { LoadingButton } from "@mui/lab";
import { Box, Paper, Typography, Grid, Button, Container } from "@mui/material";
import { useEffect } from "react";
import { useForm, FieldValues } from "react-hook-form";
import agent from "../../../app/api/agent";
import { AppDropzone } from "../../../app/components/AppDropzone";
import AppSelectList from "../../../app/components/AppSelectList";
import TextInput from "../../../app/components/TextInput";
import useCategory from "../../../app/hook/useCategory";
import useProducts from "../../../app/hook/useProducts";
import { Category, Product } from "../../../app/models/Product";
import { useAppDispatch } from "../../../app/store/configureStore";
import { setProduct } from "../../product/productSlice";
import { setCategory } from "./categorySlice";
import { validationSchema } from "./categoryValidation";


interface Props {
  category?: Category;   // ?   undefined
  cancelEdit: () => void;
}
 
export default function CategoryCU({ category, cancelEdit }: Props) {
  const { 
    control, 
    reset, 
    handleSubmit, 
    formState: { isSubmitting },
  } = useForm({
    resolver: yupResolver(validationSchema),
  });
  const { } = useCategory();
  const dispatch = useAppDispatch();

  useEffect(() => {
    if (category) reset(category);  // reset
  }, [category, reset]);


  async function handleSubmitData(data: FieldValues) {
    try {
      console.log(data)
      let response: Category;
      if (category) {
        response = await agent.Admin.updateCategory(data);
      } else {
        response = await agent.Admin.createCategory(data);
      }
      dispatch(setCategory(response));
      cancelEdit();
    } catch (error) {
        console.log(error);
    }
  }
  return (
    <Container>
      <Box component={Paper} sx={{ p: 4 }}>
        <Typography variant="h4" gutterBottom sx={{ mb: 4 }}>
          {category ? (<>EditCategory</>):(<>CreatCategory</>)}

        </Typography>
        <form onSubmit={handleSubmit(handleSubmitData)}>
          <Grid container spacing={3}>
            <Grid item xs={12} sm={12}>
              <TextInput control={control} name="name" label="Product name" defaultValue=""/>
            </Grid>
          </Grid>
          <Box display="flex" justifyContent="space-between" sx={{ mt: 3 }}>
            <Button variant="contained" color="inherit" onClick={cancelEdit}>
              Cancel
            </Button>
            
            <LoadingButton
                loading={isSubmitting}
                variant="contained"
                color="success"
                type="submit"
              >
                Submit
            </LoadingButton>
          </Box>
        </form>
      </Box>
    </Container>
  );
}
