import { createAsyncThunk, createSlice,isAnyOf } from "@reduxjs/toolkit";
import { createBrowserHistory } from "history";
import { FieldValues } from "react-hook-form";
import { toast } from "react-toastify";
import agent from "../../app/api/agent";
import { User } from "../../app/models/User";
import { setBasket } from "../basket/basketSlice";

const history = createBrowserHistory();
 
interface AccountState {
    user: User | null;
}
 
const initialState: AccountState = {
    user: null
}
 
//FieldValues   await dispatch(signInUser(data));
export const signInUser = createAsyncThunk<User,FieldValues>(
    'account/signInUser',
    async (data, thunkAPI) => {
        try {
            const userDto = await agent.Account.login(data);
            const {basket, ...user} = userDto;
            if (basket) thunkAPI.dispatch(setBasket(basket));
            localStorage.setItem('user', JSON.stringify(user));
            return user;
        } catch (error: any) {
            return thunkAPI.rejectWithValue({error: error.data});
        }
    }
)
 
export const fetchCurrentUser = createAsyncThunk<User>(
    'account/fetchCurrentUser',
    async ( _ , thunkAPI) => {
        // thunkAPI  action  slice
        thunkAPI.dispatch(setUser(JSON.parse(localStorage.getItem('user')!)))
        try {
            const userDto = await agent.Account.currentUser()
            const {basket, ...user} = userDto;
            if (basket) thunkAPI.dispatch(setBasket(basket));
            localStorage.setItem('user', JSON.stringify(user));
            return user;
        } catch (error: any) {
            return thunkAPI.rejectWithValue({error: error.data});
        }
    },
    {
        condition: () => {
            if (!localStorage.getItem('user')) return false;  //  Network request
        }
    }
)
 
export const accountSlice = createSlice({
    name: 'account',
    initialState,
    reducers: {
        signOut: (state) => {
            state.user = null;
            localStorage.removeItem('user');
            history.push('/')
            window.location.href = "/"
        },
        setUser: (state, action) => {
            // Roles  Token
            let claims = JSON.parse(window.atob(action.payload.token.split('.')[1]));
            let roles = claims['http://schemas.microsoft.com/ws/2008/06/identity/claims/role'];
            // Roles = ["Menber","Admin"]
            state.user = {...action.payload, roles: typeof(roles) === 'string' ? [roles] : roles};
        }
    },
    extraReducers: (builder => {
        builder.addCase(fetchCurrentUser.rejected, (state) => {
            state.user = null;
            localStorage.removeItem('user');
            toast.error('Session expired - please login again');
            history.push('/');
            window.location.href = "/"
        });
        builder.addMatcher(isAnyOf(signInUser.fulfilled, fetchCurrentUser.fulfilled), (state, action:any) => {
            // Roles  Token
            let claims = JSON.parse(window.atob(action.payload.token.split('.')[1]));
            let roles = claims['http://schemas.microsoft.com/ws/2008/06/identity/claims/role'];
            // Roles = ["Menber","Admin"]
            state.user = {...action.payload, roles: typeof(roles) === 'string' ? [roles] : roles};
        });
        builder.addMatcher(isAnyOf(signInUser.rejected), (state, action) => {
           throw action.payload;
        });
    })
 
})
 
export const { signOut, setUser } = accountSlice.actions

