import { yupResolver } from "@hookform/resolvers/yup";
import { LoadingButton } from "@mui/lab";
import { Box, Paper, Typography, Grid, Container, Button } from "@mui/material";
import { useEffect } from "react";
import { useForm, FieldValues } from "react-hook-form";
import agent from "../../../app/api/agent";
import { AppDropzone } from "../../../app/components/AppDropzone";
import AppSelectList from "../../../app/components/AppSelectList";
import TextInput from "../../../app/components/TextInput";
import useCategory from "../../../app/hook/useCategory";
import useProducts from "../../../app/hook/useProducts";
import useSource from "../../../app/hook/useSource";
import { Product, Review } from "../../../app/models/Product";
import { useAppDispatch, useAppSelector } from "../../../app/store/configureStore";
import SelectCategory from "../appSelectList/SelectCategory";
import SelectProduct from "../appSelectList/SelectProduct";
import SelectSource from "../appSelectList/SelectSource";
import { setReview } from "./reviewSlice";
import { validationSchema } from "./reviewValidation";

interface Props {
  review?: Review;   // ?   undefined
  cancelEdit: () => void;
}
 
export default function ReviewCU({ review, cancelEdit }: Props) {
  const { 
    control, 
    reset, 
    handleSubmit, 
    watch,
    formState: { isDirty, isSubmitting },
  } = useForm({
    resolver: yupResolver(validationSchema),
  });

  const watchFile = watch("file", null);
  const dispatch = useAppDispatch();

  useEffect(() => {
    if (review && !watchFile && !isDirty) reset(review);  
    return () => {
      if (watchFile) URL.revokeObjectURL(watchFile.preview); // URL 
    };
  }, [review, reset, watchFile, isDirty]);

  async function handleSubmitData(data: FieldValues) {
    try {
      console.log(data)
      let response: Review;
      if (review) {
        response = await agent.Admin.updateReview(data);
      } else {
        response = await agent.Admin.createReview(data);
      }
      dispatch(setReview(response));
      cancelEdit();
    } catch (error) {
        console.log(error);
    }
  }

  const { products } = useProducts();
  
  return (
    <Container>
    <Box component={Paper} sx={{ p: 4 }}>
      <Typography variant="h4" gutterBottom sx={{ mb: 4 }}>
        {review ? (<>EditComment</>):(<>Comment</>)}
      </Typography>
      <form onSubmit={handleSubmit(handleSubmitData)}>
        <Grid container spacing={3}>
          <Grid item xs={12} sm={4}>
            <TextInput control={control} name="comment" label="Comment" defaultValue=""/>
          </Grid>
          <Grid item xs={12} sm={4}>
            <TextInput control={control} type="number" name="star" label="Star" defaultValue=""/>
          </Grid>

          <Grid item xs={12} sm={4}>
          <SelectProduct
              control={control}
              items={products}
              name="productId"
              label="Product"
            />
          </Grid>

          <Grid item xs={12}>
          <Grid item xs={12}>
              <Box
                display="flex"
                justifyContent="space-between"
                alignItems="center"
              >
                <AppDropzone control={control} name="file"/>
                {watchFile ? (
                  <img
                    src={watchFile.preview}
                    alt="preview"
                    style={{ maxHeight: 200 }}
                  />
                ) : (
                  <img
                    src={review?.imageUrl}
                    alt={review?.buyerId}
                    style={{ maxHeight: 200 }}
                  />
                )}
              </Box>
            </Grid>
          </Grid>
        </Grid>
        <Box display="flex" justifyContent="space-between" sx={{ mt: 3 }}>
          <Button variant="contained" color="inherit" onClick={cancelEdit}>
            Cancel
          </Button>
          
          <LoadingButton
              loading={isSubmitting}
              variant="contained"
              color="success"
              type="submit"
            >
              Submit
          </LoadingButton>
        </Box>
      </form>
    </Box>
    </Container>
  );
}
