import { LoadingButton } from "@mui/lab";
import {
    Box,
    Button,
    Card,
    Container,
    Divider,
    Grid,
    TextField,
    Typography,
  } from "@mui/material";
  import { useEffect, useState } from "react";
  import { useParams } from "react-router-dom";
  import agent from "../../app/api/agent";
  import { useStoreContext } from "../../app/context/StoreContext";
  import NotFound from "../../app/errors/NotFound";
  import Loading from "../../app/layout/Loading";
  import { AddImageProduct, Product, Review } from "../../app/models/Product";
  import { useAppDispatch, useAppSelector } from "../../app/store/configureStore";
  import { addBasketItemAsync, removeBasketItemAsync } from "../basket/basketSlice";
  import { fetchProductAsync, productSelectors } from "./productSlice";
  import "../../css/productdetail.css"
import useReview from "../../app/hook/useReview";
import { removeReview } from "../admin/reviewCrud/reviewSlice";
import ReviewCU from "../admin/reviewCrud/ReviewCU";
import { Edit, Delete, Reviews } from "@mui/icons-material";
  
  export default function ProductDetails() {
    const { id } = useParams<{ id: any }>(); // (URL Parameters)
    const product = useAppSelector(state => productSelectors.selectById(state, id));
    const {status: productStatus} = useAppSelector(state => state.product);

    const [addimageproduct, setAddImageProduct] = useState<AddImageProduct[]>([]);

    const dispatch = useAppDispatch();
    const { basket, status } = useAppSelector((state) => state.basket);
    const [quantity, setQuantity] = useState(0);
    const item = basket?.items.find((i) => i.productId === product?.id);

    const { reviews } = useReview();
    const [editMode, setEditMode] = useState(false)
    const [selectedReview, setSelectedReview] = useState<Review | undefined>(undefined);
    const [loading, setLoading] = useState(false);
    const [target, setTarget] = useState(0);
  
    // Edit
    function handleSelectReview(review: Review) {
      setSelectedReview(review);
      setEditMode(true);
    }
  
    function handleDeleteReview(id: number) {
      setLoading(true);
      setTarget(id);
      agent.Admin.deleteReview(id)
        .then(() => dispatch(removeReview(id)))
        .catch((error) => console.log(error))
        .finally(() => setLoading(false));
    }
  
    function cancelEdit() {
      if (selectedReview) setSelectedReview(undefined);
      setEditMode(false);
    }
  
    useEffect(() => {
      if (item) setQuantity(item.quantity);
   
      if (!product) dispatch(fetchProductAsync(parseInt(id)))
    }, [id,item,dispatch,product]);
  
   
    function handleInputChange(event: any) {
      if (event.target.value >= 0) {
        setQuantity(parseInt(event.target.value));
      }
    }
   
    function handleUpdateCart() {
      if (!item || quantity > item.quantity) {
        const updatedQuantity = item ? quantity - item.quantity : quantity;
        dispatch(
          addBasketItemAsync({
            productId: product?.id!,
            quantity: updatedQuantity,
          })
        );
      } else {
        const updatedQuantity = item.quantity - quantity;
        dispatch(
          removeBasketItemAsync({
            productId: product?.id!,
            quantity: updatedQuantity,
          })
        );
      }
    }
  
    useEffect(() => {
      agent.AddImageProduct.list()
        .then((data) => setAddImageProduct(data));
    }, []);

    console.log(addimageproduct);
    console.log(reviews);

    if (editMode) return <ReviewCU review={selectedReview} cancelEdit={cancelEdit} />

    if (productStatus.includes('pending')) return <Loading message="Loading Products....." />;
 
    if (!product) return <NotFound />;
   
    return (
      <Container>
        <Card sx={{maxWidth: '100%',borderRadius: '8px',borderColor:'#212121',padding:'35px'}} variant="outlined">
          <Grid container spacing={10}>
            
            <Grid item xs={6}>
              <img src={product.imageUrl} style={{ height: 400,width: 550 }} />
              <Grid
                  spacing={8}
                  container
                  direction="row"
                  justifyContent="flex-start"
                  alignItems="flex-start"
                >
                  {addimageproduct.map((AddImpo)=>(
                      <Grid item xs={3}>
                        {AddImpo.productId === product.id ? (
                            <img src={AddImpo.imageUrl} style={{ width: 125, height: 85 }} />
                          ) : (
                            <></>
                          )}         
                      </Grid>
                  ))}
                </Grid>
            </Grid> 

            <Grid item xs={6}>
                <Typography variant="h2" sx={{ fontFamily: "initial" }}>{product.name}</Typography>

                <div className="button-Head-Product"></div>
                <br></br>
                <Typography variant="h6" color="text.secondary">
                  Category : {product.category.name}
                </Typography>
                <Typography variant="h6" color="text.secondary">
                  Source : {product.source.name}
                </Typography>
                <Typography variant="h6" color="text.secondary">
                  Quantitiy : {product.quantityInStock}
                </Typography>
                <Typography variant="h4" color="">
                  ${(product.price / 100).toFixed(2)}
                </Typography>

                <Grid container spacing={2}sx={{mt:3.8}}>
                  <Grid item xs={3}>
                      <TextField
                        variant="filled"
                        type="number"
                        label="Desired Quantity"
                        color="success"
                        fullWidth
                        value={quantity}
                        onChange={handleInputChange}
                      />
                    </Grid>

                    <Grid item xs={10} sx={{mt:-1}}>
                      <LoadingButton
                          disabled={
                            item?.quantity === quantity || (!item && quantity === 0)
                          }
                          loading={status.includes("pending")}
                          onClick={handleUpdateCart}
                          sx={{ height: "55px" }}
                          color="success"
                          size="large"
                          variant="contained"
                          fullWidth
                        >
                          {item ? "Update Quantity" : "Add to Cart"}
                      </LoadingButton>
                    </Grid>
                  </Grid>
            </Grid>
          </Grid>
          
          <Typography variant="h5" color="">
             Detail Product
             
          </Typography>
          <Box>
              {product.detail}
          </Box> 
        </Card>

        <Card sx={{maxWidth: '100%',borderRadius: '8px',borderColor:'#212121',padding:'10px',marginTop:'10px'}} variant="outlined">
          <Box component="h2">
          <Typography variant="h4" color="" sx={{textAlign: "center"}}>
              Review
          </Typography>
          <Button sx={{ mt:-10,ml:120 }} size="large" variant="contained" onClick={() => setEditMode(true)} color="success">
            Create
          </Button>
          {reviews.map((review)=>(
            <Grid key={review.id}>
              {review.productId === product.id ? (
                <Card sx={{ mt: 3,padding: 4 }}>
                  <Typography variant="h6" color="">
                    <Box>
                      Name : {review.buyerId}
                    </Box>
                    <Box>
                      Comment : {review.comment}
                    </Box>
                    <Box>
                      Star : {review.star}
                    </Box>
                    <Box>
                      Date : {review.date.split("T")[0]} 
                    </Box>
                    <Box>
                      Product : {review.product.name}
                    </Box>
                    
                  </Typography>

                  <Box sx={{ mt:2 }}>
                    <img src={review.imageUrl} style={{ width: 125, height: 85 }}/>
                  </Box>

                  <Button startIcon={<Edit />} onClick={() => handleSelectReview(review)} color="warning"/>
                    <LoadingButton
                      loading={loading && target === review.id}
                      startIcon={<Delete />}
                      color="error"
                      onClick={() => handleDeleteReview(review.id)}
                    />

                </Card>
              ) : (<></>)}
            </Grid>
          ))}

          </Box>
        </Card>
      </Container>
    );
  }
  