import { Box, Card, Container, Grid, IconButton, Menu, MenuItem, Paper } from '@mui/material';
import React, { useEffect, useState } from 'react'
import useProducts from '../../app/hook/useProducts';
import Loading from '../../app/layout/Loading';
import { useAppSelector, useAppDispatch } from '../../app/store/configureStore';
import CheckBoxBt from './CheckBoxBt';
import Pagination from './Pagination';
import ProductList from './ProductList';
import ProductSearch from './ProductSearch';
import { fetchFilters, fetchProductsAsync, productSelectors, setPageNumber, setProductParams } from './productSlice';
import RadioGroupBt from './RadioGroup';
import SortIcon from '@mui/icons-material/Sort';
import ArrowBackIosIcon from '@mui/icons-material/ArrowBackIos';

const sortOptions = [
  { value: "id", label: "Number - Low to High" },
  { value: "priceDesc", label: "Price - High to low" },
  { value: "price", label: "Price - Low to high" },
];

export default function Products() {
  const { products, category, source, filtersLoaded, metaData } = useProducts();
  const { productParams } = useAppSelector((state) => state.product);
  const dispatch = useAppDispatch();

  const [anchorEl, setAnchorEl] = React.useState<null | HTMLElement>(null);
  const open = Boolean(anchorEl);

  const handleClick = (event: React.MouseEvent<HTMLElement>) => {
    setAnchorEl(event.currentTarget);
  };
  const handleClose = () => {
    setAnchorEl(null);
  };

  const [anchorE2, setAnchorE2] = React.useState<null | HTMLElement>(null);
  const open2 = Boolean(anchorE2);
  
  const handleClick2 = (event: React.MouseEvent<HTMLElement>) => {
    setAnchorE2(event.currentTarget);
  };
  const handleClose2 = () => {
    setAnchorE2(null);
  };

  const [anchorE3, setAnchorE3] = React.useState<null | HTMLElement>(null);
  const open3 = Boolean(anchorE3);
  
  const handleClick3 = (event: React.MouseEvent<HTMLElement>) => {
    setAnchorE3(event.currentTarget);
  };
  const handleClose3 = () => {
    setAnchorE3(null);
  };
  if (!filtersLoaded) return <Loading message="Loading Products..." />;

  return (
    <>
      <Container>
        <Card sx={{borderRadius:2,p:1.5}}>
        <Box sx={{ flexGrow: 1 }}>
          <Card sx={{borderRadius:5,p:2}}>
              

              <IconButton
                aria-label="more"
                id="long-button"
                aria-controls={open ? 'long-menu' : undefined}
                aria-expanded={open ? 'true' : undefined}
                aria-haspopup="true"
                onClick={handleClick}
                color="success"
              >
                Category
              </IconButton>
              <Menu
                id="long-menu"
                MenuListProps={{
                  'aria-labelledby': 'long-button',
                }}
                open={open}
                onClose={handleClose}
                PaperProps={{
                  style: {
                    marginTop: -480,
                    marginLeft: 170,
                    maxHeight: '200px',
                    width: '250px',
                    backdropFilter: 'revert-layer',
                    background:'#f9fbe7',
                  },
                }}
              >
                <MenuItem >
                  <CheckBoxBt
                    items={category}
                    checked={productParams.category}
                    onChange={(items: string[]) =>
                      dispatch(setProductParams({ category: items }))
                    }
                  />
                </MenuItem>
              </Menu>

              <IconButton
                aria-label="more"
                id="long-button"
                aria-controls={open2 ? 'long-menu' : undefined}
                aria-expanded={open2 ? 'true' : undefined}
                aria-haspopup="true"
                onClick={handleClick2}
                color="success"
              >
                Source
              </IconButton>
              <Menu
                id="long-menu"
                MenuListProps={{
                  'aria-labelledby': 'long-button',
                }}
                open={open2}
                onClose={handleClose2}
                PaperProps={{
                  style: {
                    marginTop: -480,
                    marginLeft: 310,
                    maxHeight: '200px',
                    width: '250px',
                    backdropFilter: 'revert-layer',
                    background:'#f9fbe7',
                  },
                }}
              >
                <MenuItem >
                <CheckBoxBt
                  items={source}
                  checked={productParams.source}
                  onChange={(items: string[]) =>
                    dispatch(setProductParams({ source: items }))
                  }
                />
                </MenuItem>
              </Menu>
              <Box sx={{textAlign:"end",mt:-5}}>
              <IconButton
                aria-label="more"
                id="long-button"
                aria-controls={open3 ? 'long-menu' : undefined}
                aria-expanded={open3 ? 'true' : undefined}
                aria-haspopup="true"
                onClick={handleClick3}
                color="success"
              >
                <SortIcon/>
              </IconButton>
              <Menu
                id="long-menu"
                MenuListProps={{
                  'aria-labelledby': 'long-button',
                }}
                open={open3}
                onClose={handleClose3}
                PaperProps={{
                  style: {
                    marginTop: -450,
                    marginLeft: 1000,
                    maxHeight: '200px',
                    width: '250px',
                    backdropFilter: 'revert-layer',
                    background:'#f9fbe7',
                  },
                }}
              >
                <MenuItem >
                <RadioGroupBt
                  selectedValue={productParams.orderBy}
                  options={sortOptions}
                  onChange={(e) =>
                    dispatch(setProductParams({ orderBy: e.target.value }))
                  }
                />
                </MenuItem>
              </Menu>
              </Box>
            </Card>
          <Grid container spacing={2} sx={{mt:1.5}}>
            <Grid item xs={3}></Grid>

            <Grid item xs={6} sx={{ textAlign: "center" }}>
              <Paper sx={{ mb: 2 }}>
                <ProductSearch />
              </Paper>
            </Grid>

            <Grid item xs={3}></Grid>
          </Grid>

        </Box>

        <ProductList products={products} />

        <Box sx={{mt:2}}>
        {metaData && (
          <Pagination
            metaData={metaData}
            onPageChange={(page: number) =>
              dispatch(setPageNumber({ pageNumber: page }))
            }
          />
        )}
        </Box>
        </Card>
      </Container>
    </>
  );
}
