import {
  Typography,  Button,  TableContainer,  Paper,  Table,  TableHead,  TableRow,  TableCell,
  TableBody,  Box, Container, Card,
} from "@mui/material";
import { Edit, Delete } from "@mui/icons-material";
import { useState } from "react";
import { LoadingButton } from "@mui/lab";
import { useAppDispatch } from "../../../app/store/configureStore";
import { Category, Product } from "../../../app/models/Product";
import agent from "../../../app/api/agent";
import AppPagination from "../../product/Pagination";
import useCategory from "../../../app/hook/useCategory";
import { removeCategory } from "./categorySlice";
import CategoryCU from "./CategoryCU";
import { setPageNumber } from "../../product/productSlice";
 
export default function CategoryCrud() {
  const { categorys,metaData } = useCategory();
  const dispatch = useAppDispatch();

  const [editMode,setEditMode] = useState(false)
  const [selectedCategory, setSelectedCategory] = useState<Category | undefined>(undefined);
  const [loading, setLoading] = useState(false);
  const [target, setTarget] = useState(0);

  // Edit
  function handleSelectCategory(category: Category) {
      setSelectedCategory(category);
      setEditMode(true);
  }
  
  function handleDeleteCategory(id: number) {
    setLoading(true);
    setTarget(id);
    agent.Admin.deleteCategory(id)
      .then(() => dispatch(removeCategory(id)))
      .catch((error) => console.log(error))
      .finally(() => setLoading(false));
  }

  function cancelEdit() {
      if (selectedCategory) setSelectedCategory(undefined);
      setEditMode(false);
  }

  if (editMode) return <CategoryCU category={selectedCategory} cancelEdit={cancelEdit} />

  return (
    <Container>
      <Card sx={{borderRadius:3}}>

      <Box display="flex" justifyContent="space-between">
        <Typography sx={{ p: 2 }} variant="h4">
          CategoryList
        </Typography>
        <Button sx={{ m: 2 }} size="large" variant="contained" onClick={()=>setEditMode(true)} color="success">
          Create
        </Button>
      </Box>
      <TableContainer component={Paper}>
        <Table sx={{ minWidth: 650 }} aria-label="simple table">
          <TableHead>
            <TableRow>
              <TableCell>#</TableCell>
              <TableCell align="center">Category</TableCell>
              <TableCell align="center">Date</TableCell>
              <TableCell align="right"></TableCell>
            </TableRow>
          </TableHead>
          <TableBody>
            {categorys.map((category,index) => (
              <TableRow
                key={index}
                sx={{ "&:last-child td, &:last-child th": { border: 0 } }}
              >
                <TableCell component="th" scope="row">
                  {category.id}
                </TableCell>
                <TableCell align="center">{category.name}</TableCell>
                <TableCell align="center">{category.date.split("T")[0]}</TableCell>
                <TableCell align="right">
                  <Button startIcon={<Edit />} onClick={()=>handleSelectCategory(category)} color="warning"/>
                  <LoadingButton
                    loading={loading && target === category.id}
                    startIcon={<Delete />}
                    color="error"
                    onClick={() => handleDeleteCategory(category.id)}
                  />
                </TableCell>
              </TableRow>
            ))}
          </TableBody>
        </Table>
      </TableContainer>
      {metaData && (
        <Box sx={{ pt: 2 }}>
          <AppPagination
            metaData={metaData}
            onPageChange={(page: number) =>
              dispatch(setPageNumber({ pageNumber: page }))
            }
          />
        </Box>
      )}
      </Card>
    </Container>
  );
}
