import {
    Avatar,
    Button,
    Card,
    CardActions,
    CardContent,
    CardMedia,
    Typography,
    CardHeader,
    Grid,
    Box,
  } from "@mui/material";
import { Link } from "react-router-dom";
  import { Product } from "../../app/models/Product";
  import KeyboardArrowRightIcon from '@mui/icons-material/KeyboardArrowRight';
import agent from "../../app/api/agent";
import { useState } from "react";
import { LoadingButton } from "@mui/lab";
import ShoppingCartIcon from '@mui/icons-material/ShoppingCart';
import { useStoreContext } from "../../app/context/StoreContext";
import { useAppDispatch, useAppSelector } from "../../app/store/configureStore";
import { addBasketItemAsync, setBasket } from "../basket/basketSlice";

  interface Props {
    product: Product;
  }
  
  export default function ProductCard({ product }: Props) {
    const { status } = useAppSelector((state) => state.basket);
    const dispatch =  useAppDispatch()
  
    return (
      <>
        <Card sx={{ maxWidth: 300 ,borderRadius:5}}>
          <Grid container spacing={2}>
              <Grid item xs={9}>
                  <CardHeader
                    title={product.name}
                  />
                  <CardContent sx={{mt:-4}}>
                    <Typography variant="body2" color="text.secondary">
                      C : {product.category.name}
                    </Typography>
                    <Typography variant="body2" color="text.secondary">
                      S : {product.source.name}
                    </Typography>
                  </CardContent>

                  <Typography sx={{ml:2}} gutterBottom variant="h5" component="div" color="">
                    ${(product.price / 100).toFixed(2)}
                  </Typography>
              </Grid>
              <Grid item xs={3}>
                <LoadingButton
                  loading={status.includes("pendingAddItem" + product.id)} //includes('pending') 
                  onClick={() =>
                    dispatch(addBasketItemAsync({ productId: product.id }))
                  }
                  size="small"
                  sx={{color:"#7cb342"}}
                >
                  
                  <ShoppingCartIcon />
              </LoadingButton>
            </Grid>
          </Grid>
        <CardMedia
            component="img"
            height="200"
            sx={{backgroundSize:"container",bgcolor:'#FFF0F5'}}
            image={product.imageUrl}
            
          />
          <CardActions>
            <Button sx={{flexGrow:1}} size="large" component={Link} to={`/product/${product.id}`}>
              <KeyboardArrowRightIcon sx={{ color: "darkblue" }}/>
            </Button>
          </CardActions>
        </Card>
      </>
    );
  }
