import { useEffect } from "react";
import { couponSlectors, fetchCouponsAsync } from "../../features/admin/couponCrud/couponSlice";
import { useAppSelector, useAppDispatch } from "../store/configureStore";
 
export default function useCategory() {
    const coupons = useAppSelector(couponSlectors.selectAll);
    const { couponLoaded,metaData } = useAppSelector(state => state.coupon);
    const dispatch = useAppDispatch();
 
    useEffect(() => {
        if (!couponLoaded) dispatch(fetchCouponsAsync());
    }, [couponLoaded, dispatch])

    return {
        couponLoaded,
        coupons,
        metaData,
    }
}
