import { useEffect } from "react";
import { productSelectors, fetchProductsAsync, fetchFilters } from "../../features/product/productSlice";
import { useAppSelector, useAppDispatch } from "../store/configureStore";
 
export default function useProducts() {
    const products = useAppSelector(productSelectors.selectAll);
    const { productsLoaded, filtersLoaded, category, source, metaData } = useAppSelector(state => state.product);
    const dispatch = useAppDispatch();
 
    // useEffect  [] ( Redux dev tools)
    useEffect(() => {
        if (!productsLoaded) dispatch(fetchProductsAsync());
    }, [productsLoaded, dispatch])
 
    useEffect(() => {
        if (!filtersLoaded) dispatch(fetchFilters());
    }, [filtersLoaded, dispatch]);
 
    return {
        products,
        productsLoaded,
        filtersLoaded,
        category,
        source,
        metaData
    }
}
