import { yupResolver } from "@hookform/resolvers/yup";
import { LoadingButton } from "@mui/lab";
import { Box, Paper, Typography, Grid, Button, Container } from "@mui/material";
import { useEffect } from "react";
import { useForm, FieldValues } from "react-hook-form";
import agent from "../../../app/api/agent";
import { AppDropzone } from "../../../app/components/AppDropzone";
import AppSelectList from "../../../app/components/AppSelectList";
import TextInput from "../../../app/components/TextInput";
import useCategory from "../../../app/hook/useCategory";
import useCoupon from "../../../app/hook/useCoupon";
import useProducts from "../../../app/hook/useProducts";
import { Coupon } from "../../../app/models/Coupon";
import { Category, Product } from "../../../app/models/Product";
import { useAppDispatch } from "../../../app/store/configureStore";
import { setCoupon } from "./couponSlice";
import { validationSchema } from "./couponValidation";


interface Props {
  coupon?: Coupon;   // ?   undefined
  cancelEdit: () => void;
}
 
export default function ProductForm({ coupon, cancelEdit }: Props) {
  const { 
    control, 
    reset, 
    handleSubmit, 
    formState: { isSubmitting },
  } = useForm({
    resolver: yupResolver(validationSchema),
  });

  const { } = useCoupon();
  const dispatch = useAppDispatch();

  useEffect(() => {
    if (coupon) reset(coupon);  // reset
  }, [coupon, reset]);


  async function handleSubmitData(data: FieldValues) {
    try {
      console.log(data)
      let response: Coupon;
      if (coupon) {
        response = await agent.Admin.updateCoupon(data);
      } else {
        response = await agent.Admin.createCoupon(data);
      }
      dispatch(setCoupon(response));
      cancelEdit();
    } catch (error) {
        console.log(error);
    }
  }
  return (
    <Container>
      <Box component={Paper} sx={{ p: 4 }}>
        <Typography variant="h4" gutterBottom sx={{ mb: 4 }}>
          {coupon ? (<>EditCoupon</>):(<>CreatCoupon</>)}

        </Typography>
        <form onSubmit={handleSubmit(handleSubmitData)}>
          <Grid container spacing={3}>
            <Grid item xs={12} sm={4}>
              <TextInput control={control} name="code" label="Code" defaultValue=""
                inputProps={{
                  maxLength: 3,
                }}/>
            </Grid>
            <Grid item xs={12} sm={4}>
              <TextInput control={control} type="number" name="quantityCode" label="Quantity" defaultValue=""/>
            </Grid>
            <Grid item xs={12} sm={4}>
              <TextInput control={control} type="number" name="percentage" label="Percentage" defaultValue=""/>
            </Grid>
          </Grid>
          <Box display="flex" justifyContent="space-between" sx={{ mt: 3 }}>
            <Button variant="contained" color="inherit" onClick={cancelEdit}>
              Cancel
            </Button>
            
            <LoadingButton
                loading={isSubmitting}
                variant="contained"
                color="success"
                type="submit"
              >
                Submit
            </LoadingButton>
          </Box>
        </form>
      </Box>
    </Container>
  );
}
