import { createAsyncThunk, createEntityAdapter,createSlice } from "@reduxjs/toolkit";
import agent from "../../../app/api/agent";
import { MetaData } from "../../../app/models/Pagination";
import { Review } from "../../../app/models/Product";
import { RootState } from "../../../app/store/configureStore";

interface reviewstate {
    reviewLoaded: boolean;
    status: string;
    metaData: MetaData | null;
}

const reviewAdapter = createEntityAdapter<Review>();
 
export const fetchReviewsAsync = createAsyncThunk<Review[]>(
    'review/fetchreviewsAsync',
    async (_, thunkAPI) => {
        try {
            return await agent.Review.list();
        } catch (error: any) {
            return thunkAPI.rejectWithValue({ error: error.data }) // Interceptor
        }
    }
)

export const fetchReviewAsync = createAsyncThunk<Review,number>(
    'review/fetchreviewAsync',
    async (reviewId, thunkAPI) => {
        try {
            return await agent.Review.details(reviewId);
        } catch (error: any) {
            return thunkAPI.rejectWithValue({ error: error.data }) // Interceptor
        }
    }
)

export const reviewSlice = createSlice({
    name: 'review',
    initialState:reviewAdapter.getInitialState({
        reviewLoaded: false,
        status: 'idle',
        metaData: null,
    }),
    reducers: {
        setReview: (state, action) => {
            reviewAdapter.upsertOne(state, action.payload);
            state.reviewLoaded = false;
        },
        removeReview: (state, action) => {
            reviewAdapter.removeOne(state, action.payload);
            state.reviewLoaded = false; 
        },
        setMetaData: (state, action) => {
            state.metaData = action.payload;
        },
    },
    extraReducers: (builder => {
        builder.addCase(fetchReviewsAsync.pending, (state) => {
            state.status = 'pendingFetchreview';
        });
        builder.addCase(fetchReviewsAsync.fulfilled, (state, action) => {
            reviewAdapter.setAll(state, action.payload);
            state.status = 'idle';
            state.reviewLoaded = true;
        });
        builder.addCase(fetchReviewsAsync.rejected, (state, action) => {
            state.status = 'idle';
            console.log(action.payload)
        });

        builder.addCase(fetchReviewAsync.pending, (state) => {
            state.status = 'pendingFetchreview';
        });
        builder.addCase(fetchReviewAsync.fulfilled, (state, action) => {
            reviewAdapter.upsertOne(state, action.payload);
            state.status = 'idle';
        });
        builder.addCase(fetchReviewAsync.rejected, (state, action) => {
            state.status = 'idle';
            console.log(action.payload)
        });
    })
})
export const reviewSelctors = reviewAdapter.getSelectors((state: RootState) => state.review); 

export const { setReview,removeReview,setMetaData} = reviewSlice.actions
